
html{color:#000;overflow-y:scroll;-webkit-text-size-adjust:100%;-ms-text-size-adjust:100%
}
html *{outline:none;padding:0;margin:0;border:0;-webkit-text-size-adjust:none;-webkit-tap-highlight-color:transparent
}
html,body{height:100%;font-family:'MicroSoft Yahei','Georgia';color:rgba(0,0,0,0.9)
}
body{width:100%;background-color:#efefef;overflow-x:hidden
}
input,select,textarea{font-size:100%
}
table{border-collapse:collapse;border-spacing:0
}
ol,ul{list-style:none
}
h1,h2,h3,h4,h5,h6{font-size:100%;font-weight:500
}
sub,sup{font-size:75%;line-height:0;position:relative;vertical-align:baseline
}
sup{top:-0.5em
}
sub{bottom:-0.25em
}
ins,a,a:active,a:hover,a:visited,a:-webkit-any-link{text-decoration:none;color:rgba(0,0,0,0.6)
}
em{font-style:normal
}
html,body{height:auto;background:#fff
}
[v-cloak]{display:none
}
.centered{text-align:center
}
.left{text-align:left
}
.router-title{margin-bottom:45px
}
.router-title p{font-family:'MicroSoft Yahei';font-size:23px;color:#000;opacity:0.9;margin-bottom:10px
}
.router-title .bar{display:inline-block;width:50px;height:8px;background:#fbe795;font-size:0;line-height:0
}
p.left{text-align:left;font-size:16px;line-height:1.5
}
.news_content{text-align:center
}
.news_content p{text-indent:20px;text-align:left;font-size:18px;font-family:'MicroSoft Yahei';margin-bottom:20px
}
.news_content img{display:inline-block;margin-bottom:40px
}
@media (max-width: 768px){
.inner_router{padding:0 !important
}
}



